.card {
    
    border-radius: 2px;
    width: 160px;
    height: 150px;
    margin: 1rem;
    position: relative;
    -webkit-box-shadow: 15px 10px 10px rgb(56, 56, 56);
            box-shadow: 15px 10px 10px rgb(56, 56, 56);
    -webkit-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
  }
  
  .card > .container {
    height: 100%;
    overflow: hidden;
    
  }
  
  .card > .container > img {
    height: 100%;
  }
  
  .card:hover {
    -webkit-box-shadow: 8px 8px 20px rgb(56, 56, 56);
            box-shadow: 8px 8px 20px rgb(56, 56, 56);
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
    cursor: pointer;
  }
  
  .card:active {
    -webkit-transform: translate(1px, 1px);
        -ms-transform: translate(1px, 1px);
            transform: translate(1px, 1px);
  }


